div{
    text-align: center;
}
button {
    margin: auto;
    padding: .25rem .75rem;
    font-size: 1.2rem;
    cursor: pointer;
    background: rgb(255, 119, 0);
    color: white;
    border: 2px solid rgb(255, 119, 0);
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
  }